import {Component} from '@angular/core';
import {RestService} from './rest.service';
import {ProductService} from './product.service';

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.css']
})

export class AppComponent {
    title = 'pierwszy';
    title1 = 'no proszę....';
    title2: string = 'ss';
    title3: string = 'bvv';
    licznik = 5;
    car: Cars = new Cars();
    products: any;

    constructor(public rest: ProductService, public rest1: RestService) {
    }

    ngOnInit() {
        console.log("oninit");
        this.products='';
        this.rest1.getProducts().subscribe((data: {}) => {
            console.log(data);
            this.products = data;
        });
    }

    ngDoCheck() {
        this.title2 = this.title2 + 'ddddd---';
        this.car.getKola();
    }

    click(): string {
        this.rest1.getProducts().subscribe((data: {}) => {
            console.log(data);
            this.products = data;
        });


        this.title3 += 'ffs--' + this.car.getKola() + 'kk';
        this.car
        //this.title3+='ffffffffff----';
        this.licznik--;
        if (this.licznik == 0) {
            this.licznik = 5;
            this.title3 = '';
            this.title2 = 'wyzerowane';
            this.car.kola = '0';
        }
        return 'ssss';
    }

}



class Cars {
    kola: string = '7';
    getKola(): string {
        return this.kola;
    }
}

